@charset "UTF-8";
:root {
  --very-light-gray: #f4f4f4;
  --very-dark-gray: #6a6a6a;
  --very-dark-gray-1: #e8e8e8;
  --very-dark-grayish-blue: #3c4245;
  --dark-gray: #757575;
  --strong-orange: #d86422;
  --strong-blue: #008dc9;
  --dark-blue: #006996;
  --very-dark-blue: #131c20;
  --dark-saturated-blue: #20313b;
  --tiber: #07292F;
}

html, body {
  margin: 0;
  background: var(--very-dark-gray);
  overflow: auto;
}

body {
  background: #fff;
  font-family: "Lato", Arial, Helvetica, sans-serif;
  margin: 0 auto;
  font-size: 16px;
  line-height: 1.3;
  color: var(--very-dark-grayish-blue);
}
@media only screen and (min-width: 1080px) {
  body {
    max-width: 360px;
  }
}

a {
  text-decoration: none;
}
a:hover, a:active {
  color: var(--strong-orange);
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

h1, h2, h3, h4, h5, h6, p, ul, ol, li, a {
  word-wrap: break-word;
}

pre {
  white-space: pre-wrap;
  /* css-3 */
  white-space: -moz-pre-wrap;
  /* Mozilla, since 1999 */
  white-space: -pre-wrap;
  /* Opera 4-6 */
  white-space: -o-pre-wrap;
  /* Opera 7 */
  word-wrap: break-word;
  /* Internet Explorer 5.5+ */
}

ul, ol, li {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

img {
  margin: 0;
  width: 100%;
  height: auto;
}

p {
  margin: 8px 0;
}

.footer {
  background: var(--dark-saturated-blue);
  color: #fff;
  padding: 20px 0 0;
}
.footer .menu-list__item {
  position: relative;
  padding: 0;
  border-bottom: none;
}
.footer .menu-list__item:after {
  display: none;
}
.footer .menu-list__item--icon {
  width: 87%;
}
.footer .menu-list__item .footer-link {
  padding: 5px 10px;
  color: #fff;
  cursor: pointer;
  display: block;
  font-weight: normal;
  font-size: 14px;
  text-transform: inherit;
  text-decoration: none;
}
.footer .menu-list__item .footer-link:hover, .footer .menu-list__item .footer-link:focus, .footer .menu-list__item .footer-link:active {
  text-decoration: underline;
}
.footer .menu-list__item .footer-link__title {
  margin: 0;
  display: inline-block;
}
.footer .menu-list__item .footer-link__thumbnail-icon {
  display: inline-block;
  height: auto;
  max-width: 45px;
  vertical-align: middle;
}
.footer .menu-list__item .footer-link--no-image-upload:after {
  margin-top: 0;
}
.footer .menu-list__item .footer-link--no-image-upload .footer-link__title {
  display: inline;
}

.header {
  background: #fff;
  color: var(--very-dark-grayish-blue);
  overflow: auto;
  padding: 10px 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo-image {
  margin-bottom: 10px;
  vertical-align: middle;
  height: 51px;
  max-width: 165px;
}
.header__anchor {
  color: var(--strong-blue);
  cursor: pointer;
  display: block;
  font-weight: bold;
  text-transform: uppercase;
}
.header__nav-item {
  text-decoration: underline;
  padding: 0 5px 0 0;
  font-weight: bold;
  color: #fff;
  font-size: 14px;
}

#header__burger:after {
  content: "";
  background-image: url(/static/img/b-menu.png);
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: 50px;
  width: 25px;
  height: 25px;
  display: inline-block;
  vertical-align: middle;
  margin: 0 10px 0;
}
#header__burger:hover:after, #header__burger:focus:after {
  background-position: -26px 0;
}

.article-list__item {
  position: relative;
  border-bottom: solid 1px var(--very-dark-gray-1);
  overflow: auto;
}
.article-list__item a {
  display: block;
  color: var(--very-dark-gray);
}
.article-list__item:last-child {
  border: none;
}
.article-list__item--hero {
  border-top: 2px solid var(--very-dark-gray-1);
}
.article-list__item--hero .article-list__title {
  font-size: 18px;
  padding: 10px 15px 0;
}
.article-list__item--hero .section-list__title--homepage {
  padding: 0 15px;
}
.article-list__container {
  clear: both;
  padding: 15px 15px 5px;
  overflow: auto;
}
.article-list__container--hero {
  display: block;
  padding: 0 0 5px;
}
.article-list__container--hero .article-list__section,
.article-list__container--hero .article-list__title,
.article-list__container--hero .section-list__title,
.article-list__container--hero .article-list__subtitle {
  padding: 5px 15px 0;
}
.article-list__container a {
  display: block;
}
.article-list__title {
  color: #000;
  font-size: 18px;
  line-height: 1.1;
  word-wrap: break-all;
}
.article-list__subtitle {
  font-weight: normal;
  margin: 5px 0;
  padding-top: 5px;
}
.article-list__image {
  display: block;
  height: auto;
  margin: 0 15px 0 0;
  width: 100%;
}
.article-list__image--small {
  float: left;
  padding: 10px 15px 0 0;
  width: 50%;
}
.article-list__image--content {
  text-align: center;
}
.article-list__inner--regions .article-list__title {
  color: var(--very-dark-gray);
}
.article-list__inner--regions .article-list__subtitle {
  color: var(--very-dark-gray);
  font-size: 14px;
  margin: 0;
  padding: 0;
}
.article-list__comments-counter {
  text-transform: capitalize;
  font-size: 14px;
  padding: 0 15px 10px;
  color: var(--very-dark-gray);
  display: block;
  clear: left;
}
.article-list__comments-counter .icon {
  display: none;
}
.article-list__section {
  text-transform: uppercase;
  padding: 10px 0 0;
}
.article-list__buttons {
  display: block;
  transition: all 0.2s linear;
}
.article-list__buttons .rich-text p {
  display: inline-block;
  padding: 5px 10px 5px 0;
  width: auto;
}
.article-list__buttons .cta_button,
.article-list__buttons .rich-text p a {
  background: var(--strong-orange);
  color: #fff;
  cursor: pointer;
  border: 1px solid transparent;
  border-radius: 4px;
  display: block;
  font-size: 1em;
  padding: 5px 15px;
}
.article-list__buttons .cta_button:after,
.article-list__buttons .rich-text p a:after {
  content: "→";
  margin-left: 5px;
  display: inline-block;
}
.article-list__buttons .cta_button:hover, .article-list__buttons .cta_button:active, .article-list__buttons .cta_button:focus,
.article-list__buttons .rich-text p a:hover,
.article-list__buttons .rich-text p a:active,
.article-list__buttons .rich-text p a:focus {
  background: #fff;
  color: var(--strong-orange);
  border: 1px solid var(--strong-orange);
}

.article {
  overflow: auto;
  padding: 0 0 10px;
  color: var(--very-dark-grayish-blue);
}
.article__title {
  padding: 0 15px 10px;
  font-size: 27px;
  color: #000;
}
.article__heading {
  padding: 10px 15px;
}
.article__subtitle {
  padding: 0 15px;
  margin: 0 0 5px;
}
.article__pages-link {
  padding: 5px 15px;
}
.article__image {
  display: block;
  width: 100%;
  height: auto;
  margin: 0 auto 10px;
}
.article__social {
  display: flex;
  padding: 10px;
  border-bottom: solid 1px var(--very-light-gray);
}
.article__content {
  word-wrap: break-word;
  padding: 0 15px;
}
.article__content h1, .article__content h2, .article__content h3, .article__content h4, .article__content h5, .article__content h6 {
  padding: 5px 0;
}
.article__content a {
  color: var(--strong-orange);
  padding: 5px 0;
}
.article__content a:hover, .article__content a:focus {
  text-decoration: underline;
}
.article__content ol li, .article__content ul li {
  padding: 0 0 10px;
}
.article__content ol li p, .article__content ul li p {
  margin: 0;
  padding: 0;
}
.article__content ol {
  padding: 0 30px;
}
.article__content ol li {
  list-style-type: decimal;
}
.article__content ol li p {
  display: inline;
}
.article__content ul {
  padding: 0 30px;
}
.article__content ul li {
  list-style-type: disc;
}
.article__content .article-unordered-list .article-unordered-list__item {
  list-style-type: disc;
}
.article__content .article-numbered-list__item {
  padding: 0 0 10px;
}
.article__content .codehilite {
  padding: 0 15px;
  white-space: pre-wrap;
}
.article-numbered-list {
  padding: 0 15px;
  counter-reset: li;
}

.related-articles-up-next {
  padding: 0 0 10px;
}
.related-articles-up-next__title {
  padding: 10px 15px;
  color: #000;
  font-size: 18px;
}
.related-articles-up-next__title a {
  color: #000;
  padding: 10px 15px;
  display: block;
}
.related-articles-up-next .call-to-action {
  padding: 20px 15px 0;
}
.related-articles-up-next .call-to-action__nav-item-text {
  color: var(--dark-blue);
  font-weight: bold;
  display: block;
  outline: none;
}
.related-articles__recommended {
  padding: 10px 15px !important;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: 600;
}
.related-articles-recommended {
  overflow: auto;
  margin: 5px 0;
}
.related-articles-recommended-list__content {
  padding: 0 5px;
  clear: both;
  overflow: auto;
}
.related-articles-recommended-list__content .article-list__comments-counter {
  padding: 0;
}
.related-articles-recommended__title {
  word-wrap: break-word;
  padding: 0;
  color: #000;
  font-size: 16px;
  line-height: 1.1;
}
.related-articles-recommended__title a {
  color: #000;
}
.related-articles-recommended__desc {
  margin: 5px 0;
}
.related-articles-recommended-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.related-articles-recommended-list__item {
  margin: 0 10px 20px;
  padding: 15px 0 0;
  overflow: auto;
  clear: both;
  border-top: 1px solid #cfd4d5;
}

.related-articles-up-next .call-to-action__nav-item-text {
  text-align: left;
}
.related-articles-up-next .call-to-action__nav-item-text:after {
  display: inline-block;
  margin-left: 5px;
}
@media only screen and (min-width: 320px) {
  .related-articles-up-next .call-to-action__nav-item-text:after {
    content: ">";
  }
}
@media only screen and (min-width: 490px) {
  .related-articles-up-next .call-to-action__nav-item-text:after {
    content: "❯";
  }
}

.promoted-article-list__anchor-grid {
  display: block;
  height: auto;
}

.banner {
  margin: 5px 0 0;
}
.banner:first-child {
  margin: 0;
}
.banner__item {
  display: block;
}
.banner__image {
  display: block;
  width: 100%;
}
.banner__block {
  padding: 15px 0;
}
.banner__block .heading {
  margin-bottom: 5px;
  text-align: center;
  color: var(--dark-blue);
}
.banner__subtitle {
  text-align: center;
  padding-bottom: 5px;
  color: var(--very-dark-grayish-blue);
}

.call-to-action__nav-item {
  color: var(--very-dark-grayish-blue);
  text-decoration: none;
  font-weight: normal;
  padding: 10px 15px;
}

.call-to-action__button-text {
  color: var(--strong-orange);
  text-align: center;
  display: block;
}

.call-to-action__button-text--hyperlink {
  color: var(--strong-orange);
  text-align: center;
  display: block;
}

.call-to-action--profile {
  padding: 0 15px 15px;
}

.call-to-action--view-profile {
  padding: 0;
}

.call-to-action__nav-item--challenge {
  text-align: left;
  color: var(--very-dark-grayish-blue);
  margin: 0;
  text-decoration: none;
  text-transform: inherit;
  font-size: 20px;
}

.call-to-action__nav-item--login {
  background: #fff;
  color: var(--very-dark-grayish-blue);
  font-size: 14px;
  font-weight: bold;
  margin: 0;
  padding: 10px 0;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
}

.call-to-action__nav-item--response {
  text-transform: capitalize;
  font-weight: bold;
}

.call-to-action__nav-item-show-results {
  color: #000;
  text-decoration: underline;
  text-transform: uppercase;
}

.call-to-action__nav-item--back-home {
  color: var(--very-dark-grayish-blue);
  font-weight: bold;
}

.comments {
  background: #f0f0f0;
  overflow: auto;
}
.comments a {
  color: #000;
}
.comments p {
  padding: 0 10px;
}
.comments .call-to-action__nav-item--login {
  background: none;
  font-weight: normal;
  text-align: left;
  padding: 0 0 30px;
  margin: 0;
  font-size: 14px;
  text-decoration: underline;
}
.comments .call-to-action__nav-item--back {
  padding: 0 15px;
  font-size: 12px;
  font-weight: bold;
}
.comments .pagination .call-to-action__nav-item--back {
  padding: 0;
}
.comments .pagination .pagination__item--current {
  color: #000;
}
.comments .call-to-action__nav-item--response {
  width: auto;
}

.comment__form input[type=submit] {
  text-align: left;
  margin: 0 0 10px;
  padding: 5px 0;
}

.comment__author {
  color: #000;
}

.comment__date {
  color: #A5A5A5;
  font-style: italic;
  font-weight: 300;
  font-size: 14px;
  display: block;
  padding-left: 10px;
}

.comment__post-message {
  padding: 0 15px;
}
.comment__post-message .icon {
  display: none;
  vertical-align: middle;
}

.comment__title,
.comments__title {
  color: #000;
  padding: 10px 0;
  font-weight: bold;
  font-size: 13px;
  text-transform: uppercase;
}

.comment__title {
  padding: 10px 15px;
}

.comment__text {
  color: #000;
  padding: 0 0 10px;
  text-transform: uppercase;
}

.comment__counter {
  text-transform: uppercase;
  color: #000;
  font-size: 14px;
  vertical-align: middle;
}

.comment-counter__text {
  display: inline-block;
}

.comment-list {
  background: #f0f0f0;
  padding: 10px 0;
}
.comment-list .call-to-action__nav-item {
  color: #000;
  text-transform: capitalize;
  width: auto;
}
.comment-list .pagination .pagination__item {
  color: #000;
  display: block;
  font-size: 12px;
  font-weight: bold;
  padding: 10px 0 0 0;
  text-decoration: underline;
  text-transform: capitalize;
}

.comment-list__item {
  padding: 5px 15px;
  position: relative;
  border-bottom: 1px solid #cfd4d5;
}
.comment-list__item:first-child {
  border-top: 1px solid #cfd4d5;
}
.comment-list__item:last-child {
  border-bottom: none;
}

label {
  color: var(--very-dark-grayish-blue);
  display: block;
  font-size: 12px;
  padding: 0 0 5px 0;
  font-weight: bold;
  text-transform: uppercase;
  line-height: 1em;
}

fieldset {
  border: none;
  margin: 15px 0;
  padding: 0;
}

input[type=text],
input[type=number],
input[type=password],
input[type=email],
input[type=tel],
input[type=search] {
  border: 1px solid #d8d5d5;
  box-sizing: border-box;
  color: var(--very-dark-grayish-blue);
  font-size: 16px;
  padding: 8px 10px;
  width: 100%;
  -webkit-appearance: none;
}

select {
  font-size: 16px;
  display: block;
  margin: 10px 0;
  width: 100%;
}

textarea {
  width: 100%;
}

.search__submit,
.call-to-action__nav-item,
input[type=submit] {
  background: transparent;
  display: block;
  border-radius: 4px;
  border: none;
  color: var(--very-dark-grayish-blue);
  font-weight: bold;
  cursor: pointer;
  font-size: 14px;
  margin: 10px 0;
  text-transform: uppercase;
  text-decoration: underline;
  width: 100%;
}

.profiles__form input[type=submit] {
  color: #4D4391;
  text-decoration: underline;
  font-weight: 400;
  text-align: left;
}

.error {
  font-size: 12px;
  display: block;
  color: #ff0000;
}

.heading {
  padding: 0 0 5px;
}

.heading--subtitle {
  padding: 10px;
}

.heading--feedback-title {
  padding: 0 15px 10px;
}

.heading--large {
  font-size: 25px;
  padding: 15px 15px 0;
  text-transform: capitalize;
}

.heading--profiles,
.heading__profiles {
  color: var(--very-dark-grayish-blue);
  font-size: 25px;
  text-transform: capitalize;
  padding: 10px 15px;
}

.heading--personae {
  background: var(--very-dark-grayish-blue);
  padding: 15px;
  color: #fff;
  font-size: 16px;
  text-transform: uppercase;
}

.heading--navigation {
  color: #fff;
  background: var(--very-dark-grayish-blue);
}

.heading--homepage {
  background: transparent;
  padding: 10px 15px;
  text-transform: uppercase;
  color: #fff;
  background: var(--strong-blue);
  font-size: 16px;
}

.heading--comment {
  text-transform: uppercase;
  padding: 0 10px;
}

.comment__description {
  padding: 0 10px 30px;
  font-weight: 400;
}

.heading--search {
  text-transform: uppercase;
  font-size: 16px;
  padding: 0 15px 10px;
  border-bottom: 1px solid #B0B9BB;
}

.heading--polls {
  color: var(--strong-blue);
  font-size: 14px;
  padding: 10px 15px;
  text-transform: uppercase;
  border-top: 2px solid var(--strong-blue);
}

.heading--footerpage {
  font-size: 25px;
  padding: 5px 15px;
}

#language-selection__toggle,
#language-selection__toggle--footer {
  visibility: hidden;
  position: absolute;
}

.language {
  background: var(--very-light-gray);
  color: var(--strong-blue);
}
.language__bar {
  overflow: auto;
}
.language-current {
  color: var(--strong-blue);
  display: inline-block;
  padding: 0 10px;
  margin: 3px 4em 0 0;
  opacity: 0.8;
  text-transform: uppercase;
  font-size: 12px;
}
.language-current span {
  color: var(--strong-orange);
  font-weight: bold;
}
.language-selection {
  border-bottom: 2px solid var(--dark-blue);
}
.language-selection__toggle-input {
  display: none;
}
.language-selection__toggle-input:not(checked) ~ .language-selection {
  display: none;
}
.language-selection__toggle-input:checked ~ .language-selection {
  display: block;
}
.language-selection--footer {
  padding: 10px 0 0;
  border-bottom: none;
}
.language-selection--footer .language-list {
  background: var(--very-dark-blue);
}
.language-selection--footer .language-list__item a {
  color: var(--strong-blue);
}

.language-list {
  background: #fff;
}
.language-list__item {
  text-align: right;
  padding: 20px;
}
.language-list__item a {
  color: var(--strong-blue);
}
.language-list__item a:after {
  background: url("/static/img/arrow-down-blue.svg") no-repeat 0 0, 100%;
  content: "";
  display: inline-block;
  width: 20px;
  height: 15px;
}
.language-list__item a:hover, .language-list__item a:focus {
  color: var(--strong-orange);
}
.language-list__item a:hover:after, .language-list__item a:focus:after {
  background: url("/static/img/arrow-down-orange.svg") no-repeat 0 0, 100%;
}
.language-list__item--active a {
  text-decoration: underline;
}
.language-list__item--active a:after {
  text-decoration: none;
}

.select-language {
  color: var(--strong-blue);
  cursor: pointer;
  display: inline-block;
}
.select-language:after {
  content: "";
  background: url("/static/img/arrow-down-blue.svg") no-repeat 0 0, 100%;
  margin: 0 5px;
  display: inline-block;
  width: 20px;
  height: 15px;
}
.select-language:hover, .select-language:focus {
  color: var(--strong-orange);
}
.select-language:hover:after, .select-language:focus:after {
  background: url("/static/img/arrow-down-orange.svg") no-repeat 0 0, 100%;
}
.select-language--footer {
  padding: 0;
}
.select-language--footer:after {
  font-size: 14px;
}

.media-player {
  background-color: var(--very-dark-grayish-blue);
  margin: 5px auto;
  overflow: auto;
}
.media-player__title {
  color: #fff;
  font-size: 12px;
  text-transform: uppercase;
}
.media-player__subtitle {
  color: #fff;
  font-size: 12px;
  padding: 0 25px;
}
.media-player__caption {
  color: #fff;
  font-size: 12px;
  text-align: center;
}
.media-player__icon {
  background-image: url("/static/img/play-button.png");
  background-image: url("/static/img/play-button.svg"), linear-gradient(transparent, transparent);
  background-repeat: no-repeat;
  background-position: center 15px;
  color: #000;
  padding: 85px 0 0;
  display: block;
  font-weight: bold;
  text-decoration: underline;
}
.media-player__content {
  padding: 5px;
  text-align: center;
}
.media-player__no-player {
  text-align: center;
  padding: 10px 0;
  border: 1px solid #fff;
}
.media-player__embed-object {
  position: relative;
