.comment__form input[name~=submit_anonymously] {
  background: transparent;
  color: var(--very-dark-grayish-blue);
  display: inline-block;
  text-align: right;
  padding: 0;
}

.comment__date {
  display: inline;
}

.comment__post-message .icon {
  display: inline-block;
}

.article-list__comments-counter .icon {
  display: inline-block;
  background-size: 78%;
  vertical-align: middle;
}

.comment-counter__text {
  display: none;
  vertical-align: middle;
}

.comment-counter__number {
  padding-left: 5px;
  vertical-align: middle;
}

.comment__counter {
  font-weight: bold;
}

.comment__text {
  display: none;
}

.comment-list .comment-list__item p {
  padding: 0 50px 0 0;
}
.comment-list .pagination {
  text-align: right;
}
.comment-list .pagination .pagination__item {
  text-transform: uppercase;
  text-decoration: none;
}
.comment-list .pagination .pagination__item:after {
  padding: 0 0 0 5px;
}
@media only screen and (min-width: 320px) {
  .comment-list .pagination .pagination__item:after {
    content: ">";
  }
}
@media only screen and (min-width: 490px) {
  .comment-list .pagination .pagination__item:after {
    content: "❯";
  }
}

#comments-list .call-to-action__nav-item--response,
#comment__list .call-to-action__nav-item--response {
  position: absolute;
  right: 15px;
  top: -5px;
}

#comment__list p {
  padding: 0 50px 0 0;
}

.heading--comment {
  padding: 10px 15px;
}

.heading--profiles {
  background: var(--very-dark-grayish-blue);
  color: #fff;
}

.heading--search {
  font-size: 14px;
}

.comment__description {
  padding: 0 40px 30px 15px;
}

.comment__post-message .comment__description {
  padding: 0 40px 30px 0;
}

.menu-list__item {
  position: relative;
}
.menu-list__item a, .menu-list__item .footer-links {
  display: block;
  text-decoration: none;
}
.menu-list__item a:after, .menu-list__item .footer-links:after {
  display: inline-block;
}

@media only screen and (min-width: 320px) {
  .header__nav-item {
    text-decoration: none;
    padding: 0 0 0 10px;
  }

  .menu-links {
    display: inline-block;
    text-align: right;
    width: 58%;
  }
}
.menu-list__item--icon {
  display: inline-block;
  padding-right: 5px;
}

.personae {
  position: relative;
}
.personae a {
  text-decoration: none;
  display: block;
}

.personae-list__item {
  padding: 30px 15px 30px 45px;
  position: relative;
}
.personae-list__item .personae__image {
  display: block;
  width: auto;
  height: 100%;
}

.personae__title {
  padding: 0 0 0 60px;
}
.personae__title:after {
  display: inline-block;
  position: absolute;
  top: 40%;
  right: 10px;
}
@media only screen and (min-width: 320px) {
  .personae__title:after {
    content: ">";
  }
}
@media only screen and (min-width: 490px) {
  .personae__title:after {
    content: "❯";
  }
}

.personae-list__item--image-container {
  background: var(--strong-blue);
  border-radius: 100%;
  overflow: hidden;
  width: 65px;
  height: 65px;
  display: block;
  position: absolute;
  left: 15px;
  top: 9px;
}

.personae__skip {
  border: 1px solid #fff;
  border-radius: 5px;
  text-align: center;
  padding: 10px;
  margin: 20px 15px;
}

.polls .call-to-action__nav-item--back-home {
  padding: 10px;
}

.call-to-action__nav-item--poll-login,
.polls__form input[type=submit] {
  background: var(--strong-blue);
  color: #fff;
  text-align: center;
  text-decoration: none;
  font-weight: normal;
}

.call-to-action--polls .call-to-action__nav-item--poll-login,
.call-to-action--polls .polls__form input[type=submit] {
  background: var(--strong-blue);
  color: #fff;
}

.choice-result__percent-bar {
  background: var(--strong-blue);
  display: block;
  padding: 10px 0;
  border-radius: 10px;
}

.profiles__forgotten-password {
  font-size: 12px;
}

.call-to-action--profile {
  background: var(--very-dark-grayish-blue);
  color: #fff;
}

.call-to-action--view-profile,
.call-to-action--view-profiles {
  background: transparent;
}

input[type=text],
input[type=password],
input[type=email],
input[type=tel],
input[type=search] {
  border-radius: 4px;
}

select {
  width: 30%;
  display: inline-block;
  margin: 0 3px 0 0;
}

textarea {
  padding: 5px;
  width: 95%;
}

.call-to-action__nav-item,
input[type=submit] {
  white-space: nowrap;
  padding: 10px 0;
}

input[type=submit] {
  text-align: center;
  text-decoration: none;
  padding: 10px 0;
}

.profiles__form input[type=submit] {
  background: var(--very-dark-grayish-blue);
  color: #fff;
  margin: 10px 0;
  padding: 10px 0;
  text-align: center;
  text-decoration: none;
}

.surveys .call-to-action__nav-item--back-home {
  padding: 0 15px 10px;
}
.surveys input[type=submit] {
  width: 100%;
}
.surveys input[type=submit],
.surveys .call-to-action__nav-item--surveys {
  background: var(--strong-blue);
  color: #fff;
  text-decoration: none;
  text-align: center;
  display: block;
  text-transform: uppercase;
  font-size: 14px;
}
.surveys .section-list-nav {
  background: var(--strong-blue);
}
.surveys .section-list-nav .section-list-nav__item {
  color: #fff;
}
.surveys .section-list-nav .section-list-nav__item a {
  color: #fff;
}
.surveys .section-list-nav .section-list-nav__item span {
  color: #fff;
}

.surveys__form {
  padding: 0 15px;
}

.surveys__results,
.surveys__results {
  padding: 0 15px;
}

.versions {
  color: black;
}
.versions table, .versions th, .versions td {
  border: 1px solid black;
}
.versions th, .versions td {
  padding: 5px;
  font-size: 14px;
}
.versions th {
  text-align: left;
}
.versions a {
  color: red;
}
.versions .latest {
  color: #20C320;
}
.versions .plugin {
  font-weight: bold;
}
.versions .not-installed {
  color: orange;
}